.App {
  font-family: sans-serif;
  text-align: left;
  width: 60%;
  margin: 50px auto;
}

button {
  background-color: #e5e7eb;
  border-radius: 7px;
  margin-left: 10px;
  padding: 5px 10px;
  font-size: 15px;
  border: 1px solid black;
  cursor: pointer;
}
hr {
  margin-top: 20px;
  background-color: black;
}
.bookName {
  background-color: #efefef;
  border: 0.5px solid#858a94;
  border-radius: 5px;
  padding: 20px 15px;
  margin-top: 20px;
  list-style: none;
}
html {
  background-color: #efefef;
}
@media screen and (max-width: 455px) {
  .App {
    width: 90%;
  }
}
